import React, { Component } from 'react';
import { Button, Card, ListGroup, ListGroupItem, table, Row, Col, Container } from 'react-bootstrap';
import ReactCardCarousel from 'react-card-carousel';
import SliderPage from 'react-slider-page'
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import axios from "axios";

import pageThreeDatas from "./nftsData.json"
import pageThreeDatas2 from "./nftsData2.json"


class MyCarousel extends Component {
  state = {
    pageOneDatas: [],
    pageTwoDatas: [],
    pageThreeDatasHalf: []
  }

  //first page carousel setting
  static get CARD_STYLE() {
    return {
      marginTop: '-300px',
      height: '500px',
      width: '500px',
      textAlign: 'center',
      background: '#52C0F5',
      color: '#FFF',
      fontSize: '12px',
      borderRadius: '10px',
    };
  }

  componentDidMount() {

  //getting random assets page One
  axios.get('https://api.opensea.io/api/v1/assets?limit=3')
  .then((response) => {
    const pageOneDatas = response.data.assets
    this.setState({pageOneDatas})
  });

    //getting top creators page two
    axios.get('https://api.opensea.io/api/v1/collections?limit=300')
    .then((response) => {
      const pageTwoDatas = [];
      for(let i = 0; i < response.data.collections.length; i++){
        if(pageTwoDatas.length == 10){
          break
        } else{
          if(response.data.collections[i].large_image_url != null){
          pageTwoDatas.push(response.data.collections[i])
          }
        }
      }
        this.setState({pageTwoDatas})
    });
  }

  
  render() {
        const responsive = {
      superLargeDesktop: {
        // the naming can be any, depends on you.
        breakpoint: { max: 4000, min: 3000 },
        items: 5
      },
      desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3
      },
      tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2
      },
      mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1
      }
    };


    // window.innerWidth > 1500 ?
    const listItems = pageThreeDatas.data.nfts.map((datas) =>
      <div style={{width:"120%",display:"flex", flexDirection:"row", marginBottom:"5%", paddingTop:"0.5%", height:"14.4%", borderColor:"grey", borderStyle:"solid", borderWidth:"1px", boxShadow: "5px", borderRadius:25, backgroundColor:"hsl(260, 38%, 95%)"}}>
        <div style={{fontSize:"20px", width:"20%", height:"100%"}}>
          <img src={datas.image} style={{maxHeight:"90%", width:"50%", borderRadius:100}}/>
        </div>
        <div style={{display:"flex", flexDirection:"column",fontSize:"20px", width:"50%", height:"100%"}}>
          <div style={{flex:1, fontWeight:"bold"}} title={datas.name}>{datas.name.length < 20 ? datas.name:datas.name.substring(0,18)+"..."}</div>
          <div style={{flex:1}}>{datas.price} ETH</div>
        </div>
        <div style={{fontSize:"20px", width:"30%", height:"100%",paddingTop:"3%", fontWeight:"bold"}}>
          20%
        </div>
      </div>
    );

    const listItems2 = pageThreeDatas2.data.nfts.map((datas) =>
    <div style={{width:"120%", display:"flex", flexDirection:"row", marginBottom:"5%", paddingTop:"0.5%", height:"14.4%", borderColor:"grey", borderStyle:"solid", borderWidth:"1px", boxShadow: "5px", borderRadius:25, backgroundColor:"hsl(260, 38%, 95%)"}}>
      <div style={{fontSize:"20px", width:"20%", height:"100%"}}>
        <img src={datas.image} style={{maxHeight:"90%", width:"50%", borderRadius:100}}/>
      </div>
      <div style={{display:"flex", flexDirection:"column",fontSize:"20px", width:"50%", height:"100%"}}>
        <div style={{flex:1, fontWeight:"bold"}} title={datas.name}>{datas.name.length < 20 ? datas.name:datas.name.substring(0,18)+"..."}</div>
        <div style={{flex:1}}>{datas.price} ETH</div>
      </div>
      <div style={{fontSize:"20px", width:"30%", height:"100%",paddingTop:"3%", fontWeight:"bold"}}>
        20%
      </div>
    </div>
  );

    return (
      <div>
        <SliderPage>
        <section id="main" style={stylesPageOne.pageOneContainer}>
            <ReactCardCarousel  autoplay={ true } autoplay_speed={ 3500 }>
              {this.state.pageOneDatas.map(images =>
                     <div style={ MyCarousel.CARD_STYLE }>
                     <img src={images.image_url} style={stylesPageOne.image}/>
                   </div>
                )}
            </ReactCardCarousel>
          <div style={stylesPageOne.textBody}>
            <div style={stylesPageOne.title}>
              Discover, collect, and sell extraordinary NFTs
            </div>
            <div style={stylesPageOne.info}>
              Discover, collect, and sell extraordinary NFTs
            </div>
            <div>
              <Button style={stylesPageOne.direction}>Scroll below to explore!</Button>
            </div>
          </div>
        </section>

        <section id="nftCreators">
        <div style={stylesPageTwo.container}>
        <div style={stylesPageTwo.title}>
            <div>
            Top Creators
          </div>
            </div>
            <div style={{flex:2}}>
              {/*Replace with map for array*/}
          <Carousel autoPlay={true} responsive={responsive} sliderClass={{width:"5%"}}>
          {this.state.pageTwoDatas.map(images =>
                <div>
                  <img style={{height:"300px", width:"500px", borderRadius:10}} src={images.large_image_url} />
                  <p style={{color:"black", fontSize:"25%", fontWeight:"bold"}} title={images.name}> Artist: {images.name.length < 68 ? images.name: images.name.substring(0,68)}</p>
                </div>
                )}
          </Carousel>
          </div>
          </div>
        </section>
        
        <section id="nftList" style={stylesPageThree.container}>
          <div style={{display:"flex", flexDirection:"column"}}>
          <div style={stylesPageThree.title}>
          Top NFT Collection
          </div>
          <div style={{flex:1,}}>
            {window.innerWidth < 1500 ? 
            (
              <div style={{color:"black", marginTop:"5%", flex:1}}>
              <div style={{flex:1}}>{listItems}</div>
              </div>
            ):
            (
              <div style={{color:"black", marginTop:"5%", flex:1, flexDirection:"row", display:'flex'}}>
              <div style={{flex:1}}>{listItems}</div>
              <div style={{margin:"5%"}}></div>
              <div style={{flex:1}}>{listItems2}</div>
              </div>
            )}
          <div style={{color:"black", marginTop:"10%", fontSize:"35%", backgroundColor:"transparent", borderColor:"transparent"}}>
          Click <a  href="/home">here</a> to explore more NFTs!
          </div>
          </div>
          </div>
        </section>
        </SliderPage>
      </div>
    );
  }
}

const stylesPageOne = {
  pageOneContainer:{
    height:"100%",
    textTransform:"none",
    backgroundImage: `url("https://thumbs.dreamstime.com/b/multi-colored-watercolor-splash-abstract-vintage-canvas-background-multi-colored-watercolor-splash-abstract-colorful-vintage-115576968.jpg")`,
    backgroundSize: 'cover'
  },
  textBody:{
    height:"40%"
  },
  title:{
    paddingTop: "400px",
    fontSize: "35%",
    fontWeight:"bold",
    color: "black",
  },
  info:{
    fontSize: "20px",
    color: "black",
  },
  image:{
    width: '500px',
    height: '500px',
    borderRadius: '10px',
  },
  direction:{
    fontSize: "20px",
    color:"grey",
    border: "none",
    backgroundColor:"transparent"
  }
}

const stylesPageTwo = {
  container:{
    height:"100%",
    margin:"auto",
    width:"100%",
    display:"flex",
    flexDirection:"column",
    backgroundImage: `url("https://c.neh.tw/thumb/f/720/5469586900123648.jpg")`,
    backgroundSize: 'cover'
  },
  title:{
    flex:1,
    width:"100%",
    marginTop:"10%", 
    fontSize:"70%", 
    color:"black", 
    fontWeight:"bold"
  },
  artistName:{
    color:"black", 
    fontSize:"15%"
  },
  creatorImage:{
    flex:1,
    width:"80%",
    height:"80%"
  }
}

const stylesPageThree = {
  container:{
    height:"100%",
    // backgroundImage: `url("https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/pf-s91-pa-8919_3.jpg?w=1000&dpr=1&fit=default&crop=default&q=65&vib=3&con=3&usm=15&bg=F4F4F3&auto=format&ixlib=js-2.2.1&s=63e417dfac3c9a775bd6c91bb8499118")`,
    backgroundSize: 'cover'
  },
  title:{
    color:"black",
    fontSize:"70%",
    fontWeight:"bold",
    flex:1
  },
  cards:{
    flex:1,
    flexDirection: "column",
    color:"black",
    backgroundColor:"silver",
    fontSize:"10px",
    height:"70%",
    margin: "0.5%",
  }
}

export default MyCarousel;