import React from 'react';
import Rodal from 'rodal';
import { Navbar, Nav, Container, Button } from 'react-bootstrap';
import './Navbar.css'

// include styles
import 'rodal/lib/rodal.css';
import { Last } from 'react-bootstrap/esm/PageItem';

export default class Navigation extends React.Component {

constructor(props) {
    super(props);
    this.state = { visible: false, visible2: false };
}

show() {
    this.setState({ visible: true });
}
show2() {
    this.setState({ visible2: true });
}

hide() {
    this.setState({ visible: false });
}
hide2() {
    this.setState({ visible2: false });
}

render (){

    var calc = (window.innerWidth*20)/100
    var calc2 = (window.innerHeight*60)/100
return(
<>
    <Rodal width={calc} height={calc2} customStyles={{borderRadius:10}} visible={this.state.visible} onClose={this.hide.bind(this)}>
    <div>
        <form style={{display:"flex", flexDirection:"column", position:"inherit", fontSize:"200%"}}>
        <label style={{fontWeight:"bold"}}>Login</label>
            <label>Email</label><input style={{borderRadius:10}}></input>
            <label style={{marginTop:"5%"}}>Password</label><input style={{borderRadius:10}}></input>
            <Button style={{marginTop:"5%"}}>Login</Button>
            <Button style={{marginTop:"5%", backgroundColor:"#4267B2", borderColor:"#4267B2"}}>Facebook</Button>
            <Button style={{marginTop:"5%", backgroundColor:"#DE5246", borderColor:"#DE5246"}}>Google</Button>
            <a style={{marginTop:"5%", color:"#0d6efd"}} onClick={this.show2.bind(this)}><u>Register</u></a>
            <a style={{marginTop:"5%"}} href='/recovery'>Forgot Password</a>
        </form>
    </div>
    </Rodal>
    <Rodal width={calc} height={calc2} customStyles={{borderRadius:10}} visible={this.state.visible2} onClose={this.hide2.bind(this)}>
    <div>
        <form style={{display:"flex", flexDirection:"column", position:"inherit", fontSize:"200%"}}>
        <label style={{fontWeight:"bold"}}>Register</label>
            <label>Email</label><input style={{borderRadius:10}}></input>
            <label style={{marginTop:"5%"}}>Password</label><input style={{borderRadius:10}}></input>
            <Button style={{marginTop:"5%"}}>Register</Button>
            <Button style={{marginTop:"5%", backgroundColor:"#4267B2", borderColor:"#4267B2"}}>Facebook</Button>
            <Button style={{marginTop:"5%", backgroundColor:"#DE5246", borderColor:"#DE5246"}}>Google</Button>
        </form>
    </div>
    </Rodal>
        <Navbar collapseOnSelect fixed='top' expand='sm' bg='transparent' variant='light' >
            <Container>
                <Navbar.Toggle aria-controls='responsive-navbar-nav' />
                <Navbar.Collapse className="justify-content-end">
                    <Nav>
                        {/* <Nav.Link className='title' href='/welcome' style={{ fontSize:"30px"}}>Home</Nav.Link> */}
                        <Nav.Link onClick={this.show.bind(this)} className='title' style={{ fontSize:"30px"}}> LOGIN</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    </>
    );
}
}